.nav-container {
    display: -webkit-flex;
    display: flex;
    width: 100%;
    height: 70px;
    background-color: #222;
    position: fixed;
    z-index: 2;
    border-bottom: solid 1.3px #fff;
    -webkit-transition: 0.5s;
    transition: 0.5s
}

.menu-lists {
    -webkit-flex-direction: row;
            flex-direction: row;
    display: -webkit-flex;
    display: flex;
    width: 100%;
    list-style-type: none;
    -webkit-justify-content: flex-end;
            justify-content: flex-end;
    -webkit-align-items: center;
            align-items: center;
    margin-top: 1em;
    margin-right: 5em;
}

.menu-lists li {
    padding-right: 5em;
    font-size: 12px;
    font-weight: 500;
}

.menu-lists a {
    text-decoration: none;
    color: #fff;

}

.menu-lists a::after, .resp-menu-lists a::after {
    bottom: 0;
    left: 0;
    right: 0;
    margin: auto;
    width: 0%;
    content: '.';
    color: transparent;
    background: #aaa;
    height: 1px;
    display: block;
    margin-top: 1em;
}

.menu-lists a:hover {
    -webkit-transition: all .5s;
    transition: all .5s;
    color: #17bed2 !important;

}

.menu-lists a:hover:after {
    -webkit-transition: 0.4s;
    transition: 0.4s;
    width: 100%
}

.resp-menu-lists a:hover:after {
    -webkit-transition: 0.3s;
    transition: 0.3s;
    width: 70%;
}

.menu-lists i {
    font-size: 25px;
    display: none;
}

.menu-lists i:hover {
    color: #17bed2 !important;
}

.resp-menu-lists {
    list-style-type: none;
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
    background-color: #222;
    margin-top: 71px;
    position: absolute;
    right: 0;
    -webkit-transition: .3s;
    transition: .3s;
    -webkit-transform-origin: top;
            transform-origin: top;
    -webkit-transform: scaleY(1);
            transform: scaleY(1);
}

.resp-menu-lists li {
    padding: 0.5em 1em 0.5em 1em;
    width: 100px;
    text-align: center;
}

.resp-menu-lists a {
    text-decoration: none;
    color: #fff;
    font-size: 15px;
}

.resp-menu-lists a:hover {
    -webkit-transition: .5s;
    transition: .5s;
    color: #17bed2 !important;
}

@media only screen and (max-width: 900px) {
    .menu-lists i {
        display: inline;
        display: initial;
        position: absolute;
        top: 20%;
        right: 3.5%;
        -webkit-transition: .5s;
        transition: .5s;
        cursor: pointer;
    }
    .menu-lists a {
        display: none;
    }

}

@media only screen and (min-width: 900px) {
    .resp-menu-lists {
        -webkit-transform: scaleY(0) !important;
                transform: scaleY(0) !important;
    }
}
