.contact-container {
    background-color: #f2f2f2;
    padding: 6em 3em 6em 3em;
    border-top: 1px solid #e6e6e6;
}

.contact-title {
    display: flex;
    justify-content: space-evenly;
    flex-wrap: wrap;
    margin-right: 5em;
}


.contact-title h2 {
    text-align: end;
    color: #4d4d4d;
    font-size: 25px
}

.contact-title h5 {
    color: #4d4d4d;
}

.contact-contact-details {
    font-size: 13px;
    font-weight: 400;
    color: #666666;
    margin-bottom: 1em;
    margin-top: 0.5em;
}

.contact-details {
    margin-top: 1em;
    margin-bottom: 1em;
}

.contact-spacing {
    margin-top: 20px;
    font-weight: normal;
}

.contact-spacing a {
    text-decoration: none;
    color: #666666;
    font-weight: normal;
    margin-top: 20px;
}

.contact-spacing a:hover {
    color: #17bed2;
    transition: 0.5s;
}

.contact-details i {
    color: #4d4d4d;
    background-color: #f2f2f2;
    font-size: 35px;
}

/* responsive */

@media only screen and (max-width: 975px) {
    .contact-title {
        flex-direction: column;
        align-content: center;
        margin: 0 auto;
    }
    .contact-spacing {
        margin-top: 0.5em;
    }
    .contact-details {
        margin-top: 0.5em;
    }
    .contact-title h2 {
        text-align: initial;
    }
}